/* Shared */

/* Nav bar buttons container */
.nav_buttons {
  display: inline-block;
} 

/* Nav bar username pseudo-button */
.nav_buttons .username{
  font-size: 1rem;
}

/* Nav bar button */
.nav_button {
  margin-left: 5px;
  border: var(--main-nav-button-border);
  margin-top: 0;
  margin-bottom: 0;
  height: 40px;
  padding-top: 0;
  padding-bottom: 0;
  font-size: 1.5rem;
}

/* Nav bar color picker div */
.color-picker{
  width: 5em;
  height: 40px;
}

/* Nav bar color picker button */
.color-picker button{
  background: none;
  border: none;
  border-radius: .25rem !important;
  font-size: 1.25em;
}


/* Nav bar color picker menu */
.color-picker .dropdown-menu{  
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  min-width: 5em;
  width: 5em;
}

/* Nav bar color picker item */
.color-picker button.dropdown-item{
  height: 1.5em;
}

/* Nav bar color picker item on hover */
.color-picker button.dropdown-item:hover{
  transform: translateX(1em);
  transition: all 0.4s ease-out;
}

/* Nav bar logo */
.logo {
  padding: 0;
  width: 40px;
  height: 40px;
  display: inline-block;
  display: -webkit-inline-box;
}

/* Nav bar logo img */
.logo .logo-img{
  padding: 0;
  margin: 0;
  margin-right: 10px;
  width: 40px;
  height: 40px;
}

/* Nav bar logo name */
.logo .name{
  vertical-align: middle;
  vertical-align: -webkit-baseline-middle;
}

/* Dark */

/* Nav bar container */
.navbar-dark{
  background-color: var(--main-surface-dark);
  box-shadow: var(--main-nav-box-shadow-dark)
}
/* Nav bar button */
.nav_button.dark {
  color: var(--main-on-surface-dark);
}

/* Nav bar button on hover */
.nav_button.dark:hover{
  background-color: var(--main-nav-button-hover-dark);
  color: var(--main-on-surface-dark);
}

/* Nav bar color picker button */
.color-picker.dark button{
  color: var(--main-on-surface-dark);
}

/* Nav bar color picker button on hover */
.color-picker.dark button:hover{
  background: var(--main-nav-button-hover-dark);
  color: var(--main-on-surface-dark);
}

/* Nav bar logo */
.logo.dark .logo-img{
  background-image: url(data:image/png;base64,);
}

/* Light */

/* Nav bar container */
.navbar-light{
  background-color: var(--main-surface-light);
  box-shadow: var(--main-nav-box-shadow-light)
}

/* Nav bar button */
.nav_button.light {
  color: var(--main-on-surface-light);
}

/* Nav bar button on hover */
.nav_button.light:hover{
  background-color: var(--main-nav-button-hover-light);
  color: var(--main-on-surface-light);
}

/* Nav bar color picker button */
.color-picker.light button{
  color: var(--main-on-surface-light);
}

/* Nav bar color picker button on hover */
.color-picker.light button:hover{
  background: var(--main-nav-button-hover-light);
  color: var(--main-on-surface-light);
}

/* Nav bar logo */
.logo.light .logo-img{
  background-image: url(data:image/png;base64,);
}